class TouitApi{


    static list(timestamp, callback) {
        const request = new XMLHttpRequest();
        request.open("GET", "http://touiteur.cefim-formation.org/list?ts=" + encodeURIComponent(timestamp), true);
        request.addEventListener("readystatechange", () => {
            if (request.readyState === XMLHttpRequest.DONE) {
                if (request.status === 200) {
                    const response = JSON.parse(request.responseText);
                    callback(response);
                } else {
                    // Erreur HTTP
                }
            }
        });
        request.send();
    }

    static post(name, message, onSuccess, onError){
        let httpResquest = new XMLHttpRequest();
        httpResquest.open('POST', 'http://touiteur.cefim-formation.org/send', true);
        httpResquest.addEventListener('readystatechange', () => {
            if (httpResquest.readyState === XMLHttpRequest.DONE) {
                if (httpResquest.status === 200) {
                    const response = JSON.parse(httpResquest.responseText);
                    if (response.hasOwnProperty("success")) {
                        onSuccess(response)
                    } else if (response.hasOwnProperty("error")) {
                        onError(response.error)
                    } else {
                        onError('Erreur inconnu')
                    }
                }
            }
        })
        httpResquest.setRequestHeader('Content-type', 'application/x-www-form-urlencoded');
        httpResquest.send('name=' + encodeURIComponent(name)  + '&message=' + message);
    }

    static getTrending(callback) {
        const request = new XMLHttpRequest();
        request.open("GET", "http://touiteur.cefim-formation.org/trending", true);
        request.addEventListener("readystatechange", () => {
            if (request.readyState === XMLHttpRequest.DONE) {
                if (request.status === 200) {
                    const response = JSON.parse(request.responseText);
                    callback(response);
                } else {
                    // Erreur HTTP
                }
            }
        });
        request.send();
    }

    static getOnetouit(id){
        return fetch('http://touiteur.cefim-formation.org/get?id=' + encodeURIComponent(id) )
            .then(data => data.json())

    }

    static getComments(id){
        return fetch('http://touiteur.cefim-formation.org/comments/list?message_id=' + encodeURIComponent(id))
            .then(data => data.json())
    }
    static like(idTouit){
        let httpResquest = new XMLHttpRequest();
        let url = 'http://touiteur.cefim-formation.org/likes/send'
        httpResquest.open('PUT', url, true);
        httpResquest.setRequestHeader('Content-type', 'application/x-www-form-urlencoded');
        httpResquest.send("message_id="   + encodeURIComponent(idTouit));
    }
    static dislike(idTouit){
        let httpResquest = new XMLHttpRequest();
        let url = 'http://touiteur.cefim-formation.org/likes/remove'
        httpResquest.open('DELETE', url, true);
        httpResquest.setRequestHeader('Content-type', 'application/x-www-form-urlencoded');
        httpResquest.send("message_id="   + encodeURIComponent(idTouit));
    }
    static postComment(message_id, name, comment, onSuccess, onError){
        const httpResquest = new XMLHttpRequest();
        httpResquest.open('POST', 'http://touiteur.cefim-formation.org/comments/send', true);
        httpResquest.addEventListener('readystatechange', () => {
            if (httpResquest.readyState === XMLHttpRequest.DONE) {
                if (httpResquest.status === 200) {
                    const response = JSON.parse(httpResquest.responseText);
                    if (response.hasOwnProperty("success")) {
                        onSuccess(response)
                    } else if (response.hasOwnProperty("error")) {
                        onError(response.error)
                    } else {
                        onError('Erreur inconnu')
                    }
                }
            }
        })
        httpResquest.setRequestHeader('Content-type', 'application/x-www-form-urlencoded');
        httpResquest.send('name=' + encodeURIComponent(name)  + '&message_id=' + message_id + '&comment=' + comment);
    }
}
export default TouitApi