import React from "react";
import TouitApi from "../api/TouitApi";
import Comments from "./Comments";
import './Modale.css'

class Modale extends React.Component{

    constructor(props) {
        super(props);
        this.state = {
            "resultComments": [],
            "message_id": '',
            "name": '',
            "comment": '',
        }
    }
    inputChange = ev => {
        this.setState({
            [ev.target.name]: ev.target.value
        })
    }


    handleSubmit = (event) => {
        TouitApi.postComment(
            this.props.id,
            this.state.name,
            this.state.comment,
            () => {
                this.setState({message_id: '', name: '', comment: ''})
                TouitApi.getComments(this.props.id).then( r => this.setState({'resultComments':r.comments}))
                alert('Votre message est bien envoyé')
            },
            msg => {
                alert("Une erreur est survenue : " + msg + " !");
            })
        event.preventDefault();
    }

    componentDidMount() {
        TouitApi.getComments(this.props.id).then( r => this.setState({'resultComments':r.comments}))
    }



    closeModale = () => {
            this.props.changeStateHandler()
    }


    render(){
        const{message, name, likes} = this.props
        return(
            <section className="modale-background">
                <div className="modale">

                    <p>Auteur : {name}</p>
                    <p>Message : {message}</p>
                    <p>{likes} likes</p>
                    {
                        this.state.resultComments.length > 0 &&
                        <p>Commentaires : </p>
                    }
                    {
                        this.state.resultComments.map((obj, index) => <Comments key={index} {...obj}/>)
                    }



                    <button onClick={this.closeModale}>X</button>



                    <section className="formulaire-comments">
                        <div className="container-comments">
                            <form onSubmit={this.handleSubmit}>
                                <label>
                                    Nom :
                                </label>
                                <input type="text" name="name" value={this.state.name} onChange={this.inputChange} />

                                <label>
                                    Votre touit:
                                </label>
                                <textarea name="comment" value={this.state.comment} onChange={this.inputChange} />
                                <input className="button-submit" type="submit" value="Envoyer" />
                            </form>
                        </div>

                    </section>
                </div>


            </section>

        )
    }
}
export default Modale