import React from 'react';
import TouitApi from "../api/TouitApi";
import Trending from "./Trending";
import './SendMessageForm.css'

class SendMessageForm extends React.Component{
    constructor(props) {
        super(props);
        this.state = {name: '', message: ''};
    }


    inputChange = ev => {
        this.setState({
            [ev.target.name]: ev.target.value
        })
    }


    handleSubmit = (event) => {
        TouitApi.post(
            this.state.name,
            this.state.message,
            () => {
                this.setState({name: '', message: ''})
                alert('Votre message est bien envoyé')
            },
            msg => {
                alert("Une erreur est survenue : " + msg + " !");
            })
        event.preventDefault();
    }

    render() {
        return (
            <section className="formulaire">
                <div className="container">
                    <form onSubmit={this.handleSubmit}>
                        <label>
                            Nom :
                        </label>
                        <input type="text" name="name" value={this.state.name} onChange={this.inputChange} />

                        <label>
                            Votre touit:
                        </label>
                        <textarea name="message" value={this.state.message} onChange={this.inputChange} />
                        <input className="button-submit" type="submit" value="Envoyer" />
                    </form>
                    <Trending/>
                </div>

            </section>

        );
    }
}
export default SendMessageForm;