import React from "react";
import Header from "./components/Header";
import TouitContainer from "./components/TouitContainer";
import SendMessageForm from "./components/SendMessageForm";
import Modale from "./components/Modale";
import TouitApi from "./api/TouitApi";

class App extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            "modaleIsVisible": false,
        }

    }

    changeStateModale = (id) => {
        TouitApi.getOnetouit(id).then(r => this.modale = r.data).finally(() =>{
            this.setState({
                "modaleIsVisible": !this.state.modaleIsVisible,
            })
        })
    }

    render(){
        return (
            <div className="App">
                <Header/>
                {this.state.modaleIsVisible && <Modale changeStateHandler = {this.changeStateModale} {...this.modale}/>}
                <SendMessageForm/>
                <TouitContainer changeStateHandler = {this.changeStateModale} />
            </div>
        );
    }
}

export default App;
