import React from "react";
import TouitApi from "../api/TouitApi";
import Touit from "./Touit";
import './TouitContainer.css'

class TouitContainer extends React.Component{
    constructor(props) {
        super(props);

        this.state = {
            'result': []
        }
        this.lastTimestamp = 0;
        this.intervalId = false
    }
    // s'éxécute a la création du composant, lance un set interval
    // s'effectuant toutes les 1,5 secondes qui met à jour le composant si le nombre de resultat
    // a changer dans la base de l'API
    componentDidMount(){
        this.intervalId = setInterval(() => {
            TouitApi.list(this.lastTimestamp, resp => {
                if(resp.messages.length > 0){
                    this.setState({
                        "result": [...this.state.result, ...resp.messages]
                    });
                }
                this.lastTimestamp = resp.ts;
            });
        }, 1000)
    }

    componentWillUnmount() {
        if(this.intervalId !== false){
            clearInterval(this.intervalId)
        }
    }

    changeState = (id) => {

        this.props.changeStateHandler(id)
    }
    //map sur le state result pour créer autant de composant touit que de résultat dans la base de l'api
    render(){
        return(
            <section className="touit-container">
                <div className="container">
                    { this.state.result
                        .sort((a, b) => b.ts - a.ts)
                        .map((obj) => <Touit changeStateHandler={this.changeState} {...obj} key={obj.id}/> ) }
                </div>
            </section>
        )
    }
}
export default TouitContainer