import React from "react";
import './Touit.css';
import TouitApi from "../api/TouitApi";
class Touit extends React.Component{


    getRandomColor() {
        let letters = '0123456789ABCDEF';
        let color = '#';
        for (let i = 0; i < 6; i++) {
            color += letters[Math.floor(Math.random() * 16)];
        }
        return color;
    }

    setStateAffichageModale = (id) =>{

        this.props.changeStateHandler(id)
    }
    likeTouit = (id) =>{
        console.log(id, "the id of twit liker")
        TouitApi.like(id)
    }
    dislikeTouit = (id) =>{
        TouitApi.dislike(id)
    }

    render(){
        const {id, name, message} = this.props
        return(
            <div style={{ backgroundColor: this.getRandomColor()}} className="touit">
                <p className="touit-name">{name}</p>
                <p className="touit-message">{message}</p>
                <button onClick={this.setStateAffichageModale.bind(this, id)}>go</button>
                <button onClick={this.likeTouit.bind(this, id)}>Like</button>
                <button onClick={this.dislikeTouit.bind(this, id)}>Dislike</button>
            </div>
        )
    }
}
export default Touit